<template>
  <div>
    <van-nav-bar title="售后详情" fixed style="height: 46px">
      <van-icon name="arrow-left" size="24px" color="#666666" slot="left"  @click="$toPage(-1)" />
    </van-nav-bar>
    <div style="height: 46px;"></div>
    
    <div class="ckstatus">
      <p v-if="detail.ckstatus == -1">拒绝</p>
      <p class="description" v-if="detail.ckstatus == -1">拒绝原因: {{ detail.ckdesc }}</p>
      <p v-if="detail.ckstatus == 0">未审核</p>
      <p v-if="detail.ckstatus == -2">取消售后</p>
      <p v-if="detail.ckstatus == 200">售后完成</p>
      <p v-if="detail.ckstatus == 1">审核通过</p>
    </div>
    <!--退货退款 仅退款-->
    <div v-if="detail.astype != 3">
      <div class="info_0">
        <p>退款金额：<span>￥{{detail.refamt}}</span></p>
        <p>售后类型：{{detail.astype | asTypeFormat}}</p>
        <p>申请企业：{{storeInfo.comp.storeName}}</p>
        <p>手机号码：{{storeInfo.phone}}</p>
      </div>
      <div class="info_0 margin_top_20">
        <p>申请原因：{{detail.reasonName}}</p>
        <p>描述：{{detail.apdesc}}</p>
      </div>
      <div class="info_1 margin_top_20">
        <p>申请单号：{{detail.asno}}</p>
        <p>订单编号：{{detail.orderno}}</p>
        <p>申请时间：{{detail.apdata}} {{detail.aptime}}</p>
      </div>
      <div class="info_2 margin_top_40">
        <div class="info_2_title">一块医药</div>
        <div class="info_2_goods">
          <div class="info_2_goods_img">
            <!-- <img v-lazy="detail.imgURl" /> -->
            <img :src="detail.imgURl" />
          </div>
          <div class="info_2_goods_info">
            <div class="info_2_goods_name">{{detail.brandname}}{{detail.prodname}} {{detail.spec}}</div>
            <div class="info_2_goods_manufacturers_box">
              <div class="info_2_goods_manufacturers">{{detail.manuname}}</div>
              <div class="info_2_goods_num">x{{detail.asnum}}(退货数量)</div>
            </div>
            <div class="info_2_goods_money">退款金额：<span>￥{{detail.refamt}}</span></div>
          </div>
        </div>
        <div class="detail_div">
          <div>
            <div>优惠金额</div>
            <div>￥{{detail.distprice}}</div>
          </div>
          <div>
            <div>余额抵扣</div>
            <div>￥{{detail.balamt}}</div>
          </div>
          <div>
            <div>实付金额</div>
            <div>￥{{detail.payamt}}</div>
          </div>
          <div>
            <div>单价</div>
            <div>￥{{detail.pdprice}}</div>
          </div>
          <div>
            <div>采购数量</div>
            <div>{{detail.pnum}}</div>
          </div>
        </div>
      </div>
    </div>
    <!--补开发票-->
    <div v-if="detail.astype == 3">
      <div class="info_0">
        <p>售后类型：{{detail.astype | asTypeFormat}}</p>
        <p>申请企业：{{storeInfo.comp.storeName}}</p>
        <p>手机号码：{{storeInfo.phone}}</p>
        <!-- <p>运费：<span></span></p> -->
      </div>
      <div class="info_0 margin_top_20">
        <p>申请原因：{{detail.reasonName}}</p>
        <p>描述：{{detail.apdesc}}</p>
      </div>
      <div class="info_0 margin_top_20">
        <div class="info_type">
          <div>发票类型：{{ detail.invoicetype | invoiceType }}</div>
          <!-- <div>查看发票></div> -->
        </div>
        <p>公司名称：{{invoice.invoiceInfo.compName}}</p>
        <p>注册地址：{{invoice.invoiceInfo.address}}</p>
        <p>纳税人识别号：{{invoice.invoiceInfo.taxpayer}}</p>
        <p>开户银行：{{invoice.invoiceInfo.bankers}}</p>
        <p>银行账号：{{invoice.invoiceInfo.account}}</p>
      </div>
      <div class="info_1 margin_top_20">
        <p>申请单号：{{detail.asno}}</p>
        <p>订单编号：{{detail.orderno}}</p>
        <p>申请时间：{{detail.apdata}} {{detail.aptime}}</p>
      </div>
    </div>
    <!--图片-->
    <div class="img_box">
      <img v-for="(item, index) in fileList" :key="index" :src="item.url" @click="viewImg(index)" />
    </div>
    
    <van-image-preview
      v-model="show_img"
      :images="imgList"
      :startPosition="show_img_index"
    >
    </van-image-preview>
  </div>
</template>

<script>
  export default {
    computed: {
      storeInfo() {
        return this.$loadUserInfo();
      }
    },
    filters: {
      asTypeFormat(val) {
        let text = ''
        switch(val) {
          case 1:
          text = '退款退货'
          break
          case 2:
          text = '仅退款'
          break
          case 3:
          text = '补开发票'
          break
          case 4:
            text = "补开发票";
            break;
        }
        return text;
      },
      invoiceType(val) {
        let text = ''
        switch(val) {
          case 1:
          text = '电子普通发票'
          break
          case 2:
          text = '纸质普通发票'
          break
          case 3:
          text = '增值税专用发票'
          break
        }
        return text;
      }
    },
    data() {
      return {
        detail:'',
        invoice:'',
        fileList:[],
        imgList:[],
        show_img_index:0,//图片预览索引
        show_img:false,//图片预览
      }
    },
    mounted(){
      this.detail = JSON.parse(this.$route.query.detail);

      try {
        this.invoice = JSON.parse(this.detail.invoice)
      } catch (error) {}
      let _this = this;
      //获取售后图片
      JNB.query({
        serverName: 'globalServer',
        cls: 'FileInfoModule',
        method: 'fileServerInfo',
        params: {
          orderid: this.detail.orderno,
          compid: this.$loadUserInfo().comp.storeId
        },
        callback :(result)=>{
          result = JSON.parse(result);
          if(result.code == 200){
            let path = result.data.ergodicUrl;
            let xhr = new XMLHttpRequest();
            xhr.open("POST", path, true);
            xhr.setRequestHeader("specify-path", result.data.orderFilePath);
            xhr.setRequestHeader("ergodic-sub", "false");
            xhr.send(null);
            xhr.onreadystatechange = function() {
              if (xhr.readyState == 4 && xhr.status == 200) {
                let data = xhr.responseText;
                data = JSON.parse(data).data.sort();
                for (let i in data) {
                  if (i !== 7) {
                    _this.fileList.push({
                      uid: 0,
                      name: data[i],
                      status: "done",
                      url: result.data.downPrev +
                      result.data.orderFilePath +
                      data[i] +
                      "?" +
                      new Date().getMinutes() +
                      new Date().getSeconds() +
                      new Date().getMilliseconds()
                  });
                }
              }}
            };
          }
        },
      })
      
    },
    methods:{
      viewImg(index) {//查看图片
        this.imgList = [];
        for(let i=0;i<this.fileList.length;i++){
          this.imgList.push(this.fileList[i].url)
        }
        this.show_img = true;
        this.show_img_index = index;
      },
    }
  }
</script>

<style scoped>
  .detail_div{
    display: flex;
    justify-content: space-between;
    font-size: 28px;
    line-height: 36px;
    color: #666666;
    text-align: center;
  }
  .ckstatus{
    background: linear-gradient(to right, #fab373 , #feb5a4);
    height: 80px;
    line-height: 40px;
    padding: 40px 20px;
    color: #FFFFFF;
    font-size: 28px;
  }
  .ckstatus p{
    margin: 0;
  }
  .description{
    font-size: 24px;
  }
  .info_0{
    background-color: #FFFFFF;
    padding: 20px;
  }
  .info_0 p{
    font-size: 28px;
    margin: 0;
    line-height: 48px;
  }
  .info_0 p span{
    color: #FF0000;
    font-size: 32px;
  }
  .margin_top_20{
    margin-top: 20px;
  }
  .margin_top_40{
    margin-top: 40px;
  }
  .info_1{
    background-color: #FFFFFF;
    padding: 20px;
    font-size: 24px;
  }
  .info_1 p{
    margin: 0;
    line-height: 40px;
    color: #999999;
  }
  .info_2{
    background-color: #FFFFFF;
    padding: 20px;
  }
  .info_2_title{
    font-size: 28px;
    height: 50px;
    line-height: 50px;
    border-bottom: 1px solid #EEEEEE;
  }
  .info_2_goods{
    padding: 20px 0;
    display: flex;
  }
  .info_2_goods_img{
    width: 140px;
    height: 140px;
    margin-right: 20px;
  }
  .info_2_goods_img img{
    width: 140px;
    height: 140px;
    display: block;
  }
  .info_2_goods_info{
    display: flex;
    height: 140px;
    flex-direction: column;
    justify-content: space-between;
    flex: 1;
  }
  .info_2_goods_name{
    font-size: 24px;
    color: #333333;
    overflow: hidden;
    white-space: nowrap; 
    text-overflow: ellipsis;
    line-height: 32px;
    height: 32px;
  }
  .info_2_goods_manufacturers_box{
    display: flex;
    justify-content: space-between;
    font-size: 24px;
  }
  .info_2_goods_manufacturers{
    color: #999999;
  }
  .info_2_goods_num{
    color: #f69a23;
  }
  .info_2_goods_money{
    font-size: 24px;
    color: #666666;
    text-align: right;
  }
  .info_2_goods_money span{
    color: #FF0000;
  }
  .info_type{
    font-size: 28px;
    display: flex;
    justify-content: space-between;
  }
  .img_box{
    padding: 20px;
    background-color: #FFFFFF;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  .img_box img{
    display: block;
    width: 350px;
    height: 400px;
    margin-bottom: 20px;
  }
</style>
